<template>
    <div>
        <section class="blog-home" v-cloak>
            <search></search>
            <div class="container po-r">
                <span class="block-title"></span>
                <div class="sec-title">
                    <img class="tree-logo" src="../assets/imgs/index/logo-input.png">
                    <img src="../assets/imgs/index/tree-chart.png" usemap="#hotmap">
                </div>
            </div>
            <map name="hotmap" id="hotmap">
                <area shape="rect" coords="140,220,250,260" href ="/#/genea" alt="">
            </map>

            <div class="icon-text">
                <span class="block-title"></span>
                <div v-for="(item, i) in icontexts" :key="i">
                    <i :class="'iconfont icon-' + item.class"></i>
                    <span v-text="item.sub"></span>
                    <p v-text="item.text"></p>
                </div>
            </div>

            <div class="icon-group po-r">
                <span class="block-title"></span>
                <div class="left-icons">
                    <router-link tag="div" to="/genea" v-for="(item, i) in icons" :key="i">
                        <i :class="'iconfont icon-' + item.class"></i>
                        <span v-text="item.text"></span>
                    </router-link>
                </div>
                <img src="../assets/imgs/index/jianguan.png" width="16%">
            </div>
        </section>

        <section class="sponsors-section" style="background-color: #2891d1"></section>
    </div>
</template>

<script>
import Search from '@/components/search'

export default {
    components: {
        Search
    },

    data () {
        return {
            icontexts: [
                {class: 'xitongtupianziyuan', sub: '', text: '+'},
                {class: 'phone', sub: '', text: '......'},
                // { class: 'duanxinzhongxin', sub: '', text: ': 08566995166.....' },
                {class: 'danganheji', sub: '', text: '......'}
            ],

            icons: [
                {class: '111', text: ''},
                {class: 'renyuandanganguanli', text: ''},
                {class: 'cross', text: ''},
                {class: 'daocha', text: ''},
                {class: 'zhusheqi', text: ''},
                {class: 'wuzicaigou', text: ''},
                {class: 'jianshiqicai01', text: ''},
                {class: 'goat', text: ''}
            ]
        }
    }
}
</script>

<style lang="stylus">
@import '../assets/css/color'

img
    max-width 100%
.po-r
    position relative

#chart-container
    .title
        background-color #fff
        color color-main
        font-weight bold
        border 2px solid color-main
        border-radius 0
    .lines
        [class*='Line']
            border-color color-main
        .downLine
            background-color color-main
    .node
        cursor pointer

.icon-text
    display flex
    justify-content space-between
    background-color color-main
    position relative
    padding 0 10% 50px
    >div
        width 25%
        text-align center
        color #fff
        margin-top 40px
        i
            display block
            font-size 50px
        span
            font-weight bold
            font-size 16px
            display block
            margin 5px 0
        p
            padding 0 10px
            text-align justify
.block-title
    position absolute
    display inline-block
    width 8em
    text-align center
    top 0
    left 50%
    transform translate(-50%, -50%)
    background-color color-main
    color #fff
    border 2px solid #fff
    border-radius .2em
    padding .4em 0
    font-weight bold

.icon-group
    padding 30px 10% 60px
    .left-icons
        display inline-block
        width 75%
        cursor pointer
        >div
            display inline-block
            vertical-align middle
            width 24%
            color color-main
            text-align center
            i
                font-size 50px
                display block
                margin 15px auto 5px

.tree-logo
    display block
    margin 30px auto 10px
    width 105px
.sec-title
    margin-bottom 10px
    overflow hidden
    text-align center
</style>



