export const isReqSuccessful = (res) => {
    if (res === void 0) {
        return
    }
    if (!(res && res.meta)) {
        app.$message.error('')
        return false
    }

    if (res.meta.code === 0) {
        return true
    } else {
        app.$message.error(res.meta.errorMsg || '')
    }
}

export const resetFile = dom => {
    if (({}).toString.call(dom) !== '[object HTMLInputElement]') {
        throw new Error('DOM')
    }

    dom.value = ''
    dom.type = 'text'
    dom.type = 'file'
}

let jump = (msg, name) => {
    app.$message.success(msg)
    // here app.$router is undefined

    let id = window.location.hash.substr(8).split('/')[0]
    setTimeout(() => {
        window.location.assign(window.location.origin + '/#/admin/' + id + '/' + name + '/list')
    }, 600)
}
export const postJump = routename => {
    jump('', routename)
}
export const patchJump = routename => {
    jump('', routename)
}

export const addressToArray = place => {
    let arr = []
    let index = 0
    if (place.indexOf('') !== -1) {
        index = place.indexOf('') + 1
    } else if (place.indexOf('')) {
        index = place.indexOf('') + 3
    } else {
        // 
        index = 3
    }
    arr[0] = place.substr(0, index)
    if (place.indexOf('') !== -1) {
        arr[1] = place.substring(index, place.indexOf('') + 1)
    }
    if (place.indexOf('') !== -1 || place.indexOf('') !== -1) {
        arr[2] = place.substr(place.indexOf('') + 1)
    }
    // console.log(arr)
    return arr
}



