export const validatePassword = (pass = '') => /^[a-zA-Z0-9_]{6,20}$/.test(pass) ? true : '6-20'

export const validateQQ = qq => /^$|[1-9]\d{4,9}$/.test(qq) ? true : 'qq'

export const validateUsername = name => /^[a-zA-Z0-9]{4,12}$/.test(name) ? true : 'a-z4-12'

export const validateEmail = email => /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/.test(email) ? true : ''

export const validateTelephone = telephone => /^1[34578]\d{9}$/.test(telephone) ? true : ''



